import React, { Component } from "react";
import { Container } from "react-bootstrap";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";
import Image from "react-bootstrap/Image";
import "./waitingPage.css";
class waitingPage extends Component {
  render() {
    return (
      <Container fluid>
        <div className="backgroundColor desktop">
          <Row className="backTel desktop">
            <Row classnma="rowLogo backTel">
              <Col className="colTitle" xs={12} md={6}>
                <h3 className="title2">Le nouveau pourboire digital</h3>
              </Col>
              <Col className="colLogo" xs={12} md={6}>
                <div className="logoDiv">
                  <img className="logo" src="/images/LogoOk.png"></img>
                </div>
              </Col>
            </Row>
          </Row>
        </div>
        <div className="title">
          <p className="titleMove">Tipourboire arrive bientôt !</p>
        </div>
      </Container>
    );
  }
}

export default waitingPage;
