import React, { Component } from "react";
import Button from "react-bootstrap/Button";
import "./Footer.css";
import {
  Container,
  Navbar,
  Nav,
  Form,
  FormControl,
  NavbarBrand,
} from "react-bootstrap";

class Footer extends Component {
  render() {
    return (
      <div className="blocFooter footerOrdi">
        <Navbar sticky="bottom" bg="dark" variant="dark">
          <Navbar.Brand href="#home">
            {" "}
            <img
              src="images/icone.png"
              width="90"
              height="80"
              className="d-inline-block align-top"
              alt="React Bootstrap logo"
            />
          </Navbar.Brand>

          <Nav className="mr-auto">
            <Nav.Link href="#mentionlegale">Mentions Légales</Nav.Link>
            <Nav.Link href="#adc">Avis de confidentialité</Nav.Link>
            <Nav.Link href="#cgu">CGU</Nav.Link>
          </Nav>
          <Nav className="mr-auto2">
            <Nav.Link href="#service">Service</Nav.Link>
            <Nav.Link href="#contact">Contact</Nav.Link>
          </Nav>
          <Navbar.Brand className="social" href="#followus">
            Rejoignez notre communauté
            <div className="icons">
              <i class="fab fa-facebook-square"></i>
              <i class="fab fa-instagram-square"></i>
              <i class="fab fa-linkedin"></i>
            </div>
          </Navbar.Brand>
        </Navbar>
      </div>
    );
  }
}

export default Footer;
